import { useEffect, useState } from 'react';
import { FinancialApi } from '../../utils/adminApi';
import { Table } from 'antd';
const { Column } = Table;

const Financial = () => {
  const [tableData, setTableData] = useState();

  useEffect(() => {
    FinancialApi().then((res) => {
      setTableData(
        res.data.map((item) => ({
          ...item,
          _id: item._id.slice(0, 7),
          createdAt: item.createdAt.replaceAll(/[TZ]/g, ' '),
          updatedAt: item.updatedAt.replaceAll(/[TZ]/g, ' '),
        }))
      );
    });
  }, []);


  return (
    <Table dataSource={tableData} rowKey="_id">
      <Column title="会员编号" dataIndex="user" />
      <Column title="资金变动" dataIndex="amount" />
      <Column title="余额" dataIndex="blance" />
      <Column title="时间" dataIndex="createdAt" />
      <Column title="描述" dataIndex="description" />
    </Table>
  );
};

export default Financial;
